* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.card {

    background-color: #FEFDFD;
    border: none;
    outline: none;
    display: grid;
    gap: 30px;
    grid-template-rows: 1fr;
    grid-template-columns: 1fr 2fr;
    align-items: center;
    border-radius: 10px;
    box-shadow: 1px 1px 10px gray;
    padding-left: 15px;
}

.thumbnail img {
    border-radius: 10px;

}

.content {
    display: flex;
    gap: 10px;
    flex-direction: column;
}

@media (min-width:391px) {
    .card {
        height: 150px;
        width: 700px;
    }
}
@media (max-width:390px) {
    .card {
        height: 150px;
        width: 350px;
    }
}