* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.userpage {
    gap: 50px;
    background-color: #F8F5F5;

}

@media (max-width:390px) {
    .userpage {
        display: grid;
        grid-template-columns: 1fr ;
    }
}

@media (min-width:391px) {
    .userpage {
        display: grid;
        grid-template-columns: 1fr 4fr;

    }
}